/* ********************************************************************************* */

/********************************* BEGIN Loading Icon ***************************** */
.lds-ring {
  position: fixed;
  z-index: 999;
  width: 50px;
  height: 50px;
  overflow: visible;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0px;
}

.lds-ring:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 45px;
  height: 45px;
  margin: 6px;
  border: 6px solid #4C9E00;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #4C9E00 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* ********************************************************************************* */

/********************************* BEGIN DropDown ********************************** */
.selectListing {

  .show.dropdown {
    .dropdown-toggle:after {
      -webkit-transform: rotate(-135deg);
      -ms-transform: rotate(-135deg);
      transform: rotate(-135deg);
    }
  }

  .dropdown-toggle {
    min-width: 100%;
    height: 30px;
    box-shadow: none !important;
    background: transparent !important;
    border: 0 !important;
    color: #666 !important;
    font-size: 12px;
    padding: 2px 25px 2px 10px;
    text-align: left;

    &:after {
      right: 8px;
      top: 16px;
      position: absolute;
      border-top: 0px;
      border-left: 0px;
      border-bottom: 2px solid #333;
      border-right: 2px solid #333;
      content: '';
      display: block;
      height: 8px;
      margin-top: -5px;
      pointer-events: none;
      position: absolute;
      right: 10px;
      top: 50%;
      -webkit-transform-origin: 66% 66%;
      -ms-transform-origin: 66% 66%;
      transform-origin: 66% 66%;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      -webkit-transition: all 0.15s ease-in-out;
      transition: all 0.15s ease-in-out;
      width: 8px;
    }
  }

  .dropdown-menu {
    padding: 5px 0px;
    min-width: 135px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);

    li {
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      cursor: pointer;
      line-height: 25px;
      list-style: none;
      font-size: 12px;
      min-height: 25px;
      outline: none;
      text-align: left;
      transition: all 0.2s;
    }
  }
}

