/* ************************** Customize Input checkbox ***************************** */

.form-checkbox {
  display: flex;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* Hide the browser's default checkbox */
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    &:checked~.skin:after {
      display: block;
    }
  }

  /* Create a custom checkbox */
  .skin {
    position: absolute;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: rgba(216, 216, 216, 0.17);
    border: 1px solid #979797;
    box-shadow: inset 1px 1px 3px 0 rgba(0, 0, 0, 0.33), 1px 2px 4px 0 rgba(0, 0, 0, 0.15);

    /* Create the checkmark/indicator (hidden when not checked) */
    &:after {
      content: "";
      position: absolute;
      display: none;
      left: 5px;
      top: 1px;
      width: 6px;
      height: 11px;
      border: solid #EB583B;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }

  span,
  label {
    margin-left: 24px;
    font-size: 12px;
  }

}

.wf-inline-checkbox {
  display: flex;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* Hide the browser's default checkbox */
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    &:checked~.skin:after {
      display: block;
    }
  }

  /* Create a custom checkbox */
  .skin {
    position: absolute;
    top: 0;
    left: 0;
    height: 14px;
    width: 14px;
    background-color: #F3F3F3;
    border: 1px solid #979797;
    border-radius: 4px;

    /* Create the checkmark/indicator (hidden when not checked) */
    &:after {
      content: "";
      position: absolute;
      display: none;
      left: 1px;
      top: 1px;
      width: 10px;
      height: 10px;
      background: #EB583B;
      border-radius: 2px;
    }
  }

  span {
    margin-left: 24px;
  }

}

/* ********************************************************************************* */

/* ************************** Customize Input Radio buttons ************************ */
.radio-container {
  display: block;
  position: relative;
  padding-left: 20px;
  cursor: pointer;
  font-size: 12px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* Hide the browser's default radio button */
  input {
    position: relative;
    opacity: 0;
    cursor: pointer;

    &:checked~.checkmark:after {
      display: block;
    }
  }

  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 3px;
    left: 0;
    height: 14px;
    width: 14px;
    border: 1px solid #EB583B;
    border-radius: 7px;
    background-color: #FFFFFF;

    /* Create the indicator (the dot/circle - hidden when not checked) */
    &:after {
      content: "";
      position: absolute;
      display: none;
      top: 1.5px;
      left: 1.5px;
      width: 10px;
      height: 10px;
      background: #EB583B;
      border-radius: 6px;
      border: 0.5px solid #EB583B;
